.navbar{
    background-color: rgb(71, 69, 69);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position:sticky;
    top:0;
    z-index: 20;

}

.nav-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    max-width: 1500px;
}

.nav-logo{
    color:white;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size:2rem;
    flex-grow:1;
}

.nav-menu{
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
}

.nav-link{
    color:white;
    text-decoration: none;
    padding:0.5rem 1rem;
    height: 100%;
    border-bottom:3px solid transparent;
}

.nav-item{
  line-height: 40px;
  margin-right:1rem;
}

.nav-item:after{
    content: "";
    display: block;
    height:3px;
    width:0;
    background:transparent;
    transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after{
    width: 100%;
    background-color: #ffdd40;
}

.nav-item .active{
    color:#ffdd40;
    border:1px solid #ffdd40;
}

.change{
    display: none;
}

@media screen and (max-width:960px){
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        border-top:1px solid #fff;
        position: absolute;
        top:80px;
        left:-110%;
        opacity:1;

    }

    .nav-menu .active{
        background-color: #1f5156;
        left:0px;
        opacity:1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .change{
        display: block;
    }
}